import Vue from "vue";
import axios from 'axios'
import { Message } from 'element-ui'

// 创建实例
const request = axios.create({
  // baseURL: process.env.VUE_APP_URL, // 根据环境自动变化
  timeout: 10000
})

// 添加请求拦截器
request.interceptors.request.use(config => {
  return config
}, error => {
  Message.error('请求失败')
  console.log(error, '请求没发出去')
  return Promise.reject(error)
})

// 添加响应拦截器
request.interceptors.response.use(response => {
  Message.success('请求成功')
  return response.data
}, error => {
  Message.error('请求失败')
  if (error.response) {
    console.log(error.response.status, error.response.statusText,'请求成功发出，且服务器也响应了状态码，但状态代码超出了 2xx 的范围')
  } else if (error.request) {
    console.log(error.request, '请求已经成功发起，但没有收到响应')
  } else {
    console.log(error, '请求发送出去了，但没找到服务器') // 场景：开了http://localhost:8899端口做服务监听，但访问了http://localhost:88995时
  }
  return Promise.reject(error)
})

Vue.prototype.$request = request;
export default request
